import { useFormContext } from 'react-hook-form';
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Input } from '../ui/input';
import { HTMLInputTypeAttribute } from 'react';

interface Props {
    className?: string;
    name: string;
    label?: string;
    placeholder?: string;
    type?: HTMLInputTypeAttribute;
    disabled?: boolean;
}

export default function TextInputForm({ className, name, label, placeholder, type, disabled = false }: Props) {
    const { control } = useFormContext();

    // Add this handler to prevent scroll-induced value changes
    const handleWheel = (e: React.WheelEvent<HTMLInputElement>) => {
        if (type === 'number') {
            e.currentTarget.blur();
        }
    };
    return (
        <FormField
            control={control}
            name={name}
            render={({ field }) => (
                <FormItem className={className}>
                    <FormLabel className="text-primery-blue-900 text-nowrap font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                        {label}
                    </FormLabel>
                    <FormControl>
                        <Input
                            {...field}
                            type={type}
                            className="placeholder:text-white-blue-600"
                            placeholder={placeholder}
                            onWheel={handleWheel}
                            disabled={disabled}
                        />
                    </FormControl>
                    <FormMessage />
                </FormItem>
            )}
        />
    );
}
